.online-education-main {
  background: url('../images/online-education-page-background-original.png');
  background-repeat: no-repeat;
  background-size: auto;
  /* padding: 4rem 0; */
}

.online-education-top-wrapper {
  display: flex;
  width: 70vw;
  margin: auto;
  flex-direction: row-reverse;
  flex-wrap: wrap;
  justify-content: space-evenly;
  background-color: #f9f9f9;
  box-shadow: 7px 7px 20px 3px rgb(182, 181, 181);
  padding: 6rem 0 20rem 0;
}

.online-education-right-element {
  width: 40%;
  text-align: end;
}

@media (max-width: 950px) {
  .online-education-right-element {
    width: 90%;
  }
  .online-education-top-wrapper {
    padding: 2rem 0 2rem 0;
  }
}

